<template>
<div class="buy">
  <div class="buyheader">
    <h1>单品订单</h1>
  </div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>单品订单</el-breadcrumb-item>
  </el-breadcrumb>
  <ve-histogram :data="chartData" :settings="chartSettings"></ve-histogram>
  <el-tree :data="buys" accordion @node-click="handleNodeClick">
  </el-tree>
</div>
</template>

<script>
import {
  buys
} from '~/data'

export default {
  data() {
    return {
      buys: buys
    }
  },
  methods: {
    handleNodeClick(data) {
      console.log(data);
    }
  },
  created: function () {
      this.chartData = {
        columns: ['日期', '订单量', '处方药', '盈利', '其他'],
        rows: [
          { '日期': '1月1日', '订单量': 1523, '处方药': 1523, '盈利': 0.32, '其他': 100 },
          { '日期': '1月2日', '订单量': 1223, '处方药': 1523, '盈利': 0.345, '其他': 100 },
          { '日期': '1月3日', '订单量': 2123, '处方药': 1523, '盈利': 0.5, '其他': 100 },
          { '日期': '1月4日', '订单量': 4123, '处方药': 1523, '盈利': 0.31, '其他': 100 }
        ]
      }
      this.chartSettings = {
        axisSite: { right: ['盈利'] },
        yAxisType: ['KMB', 'percent'],
        yAxisName: ['数值', '比率']
      }
    }
};
</script>

<style lang="scss" scoped>


.el-tree {
    padding:0 15px 15px 15px;
}

.ve-histogram{
  padding: 15px;
}


</style>



